<template>
  <Infor class="mb-5" :workerList="workerList" :comapnyList="comapnyList" :orderList="orderList" />
  <Bar class="mb-5" />
  <!-- <Case class="mb-5" /> -->
  <div class="row row-cols-1 row-cols-md-2 g-4 mb-5">
    <div class="col">
      <div class="card h-100">
        <Q1 />
      </div>
    </div>
    <div class="col">
      <div class="card h-100">
        <Line />
      </div>
    </div>
  </div>
  <div class="row row-cols-1 row-cols-md-2 g-4 mb-5">
    <div class="col">
      <div class="card h-100">
        <Calendar />
      </div>
    </div>
    <div class="col">
      <div class="card h-100 py-3">
        <h3 class="text-center">代辦事項</h3>
        <Todolist />
      </div>
    </div>
  </div>
</template>

<script>
import Infor from '@/components/home/InfoCard.vue';
import Bar from '@/components/home/D3Model.vue';
import Q1 from '@/components/home/BarModel.vue';
import Line from '@/components/home/LineModel.vue';
import Calendar from '@/components/home/CalendarModel.vue';
import Todolist from '@/components/home/TodoList.vue';
import {
  apiGetWorkers,
  apiGetCompanys,
  apiGetOrders,
} from '@/assets/javascript/api';

export default {
  data() {
    return {
      workerList: [],
      comapnyList: [],
      orderList: [],
    };
  },
  components: {
    Infor,
    Bar,
    Q1,
    Line,
    Calendar,
    Todolist,
  },
  methods: {
    getWorkers() {
      apiGetWorkers()
        .then((res) => {
          this.workerList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getCompanys() {
      apiGetCompanys()
        .then((res) => {
          this.comapnyList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getOrders() {
      apiGetOrders()
        .then((res) => {
          this.orderList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.getWorkers();
    this.getCompanys();
    this.getOrders();
  },
};
</script>
