<script>
export default {
  data() {
    return {
      workerNum: 0,
      companyNum: 0,
      orderNum: 0,
      total: 0,
    };
  },
  props: ['workerList', 'comapnyList', 'orderList'],
  watch: {
    workerList() {
      this.workerNum = this.workerList.length;
    },
    comapnyList() {
      this.companyNum = this.comapnyList.length;
    },
    orderList() {
      this.orderNum = this.orderList.length;
      this.total = 0;
      this.orderList.forEach((item) => {
        this.total += item.total;
      });
    },
  },
};
</script>
