<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <section class="container">
    <!-- input -->
    <div class="input-group mb-3">
      <input
        type="text"
        class="form-control"
        placeholder="請填寫代辦事項..."
        aria-label="Recipient's username"
        aria-describedby="button-addon2"
        v-model="title"
      />
      <button
        class="btn btn-secondary"
        type="button"
        id="button-addon2"
        @click="postTodo"
      >
        <i class="bi bi-plus-lg h3 p-3"></i>
      </button>
    </div>
    <!-- form -->
    <template v-for="(item, index) in todoList" :key="index">
      <div
        class="d-flex justify-content-between align-items-center ps-3 border-bottom border-1"
        :class="[
          item.isCheck
            ? 'border-primary-subtle text-primary-emphasis bg-primary-subtle'
            : '',
        ]"
      >
        <div class="form-check">
          <input
            class="form-check-input h5"
            type="checkbox"
            value=""
            :id="index"
            @click="editTodo(item)"
            :checked="item.isCheck"
          />
          <label class="form-check-label" :for="index">
            <div class="d-flex flex-column h4">
              {{ item.title }}
            </div>
          </label>
        </div>
        <div>
          <!-- <button class="btn" @click="postTodo">
            <i class="bi bi-pencil"></i>
          </button> -->
          <button class="btn" @click="deleteTodo(item._id)">
            <i class="bi bi-trash3 h4"></i>
          </button>
        </div>
      </div>
    </template>
  </section>
</template>

<script>
import {
  apiGetTodos,
  apiPostTodo,
  apiPatchTodo,
  apiDeleteTodo,
} from '@/assets/javascript/api';

export default {
  data() {
    return {
      todoList: [],
      title: '',
      tempData: {},
    };
  },
  methods: {
    getTodos() {
      apiGetTodos()
        .then((res) => {
          this.todoList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    postTodo() {
      apiPostTodo({ title: this.title })
        .then((res) => {
          console.log(res);
          this.title = '';
          this.getTodos();
        })
        .catch((err) => {
          this.title = '';
          console.log(err);
        });
    },
    editTodo(data) {
      apiPatchTodo(data)
        .then((res) => {
          console.log(res);
          this.getTodos();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    deleteTodo(id) {
      apiDeleteTodo(id)
        .then((res) => {
          this.getTodos();
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.getTodos();
  },
};
</script>
